import React from "react";

import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import IconButton from "@material-ui/core/IconButton";
import GitHubIcon from "@material-ui/icons/GitHub";
import LinkedInIcon from "@material-ui/icons/LinkedIn";

import {Spring} from "react-spring/renderprops";

import LogoComponent from "./LogoComponent";

//#156eac

class MainPage extends React.Component {
    constructor(props) {
        super(props);

        this.handleAboutClick = this.handleAboutClick.bind(this);

        this.state = {
            svg: null
        };
    }

    componentDidMount() {
        document.getElementsByClassName("appBody")[0].classList.add("landingPage");
    }

    componentWillUnmount() {
        document.getElementsByClassName("landingPage")[0].classList.remove("landingPage");
    }

    handleAboutClick() {
        this.props.onAboutClick();
    }

    render() {
        return (
            <Grid
                container
                justify="center"
                alignItems="center"
                direction="column"
                style={{height: "99vh", width: "99vw", overflow: "hidden"}}
                spacing={4}
            >
                <Grid item>
                    <LogoComponent style={{width: "80vw", height: "100%"}} />
                </Grid>
                <Grid item>
                    <Grid container direction="row" justify="center" alignItems="center" spacing={4}>
                        <Grid item>
                            <Button variant="contained" size="large" color="primary" onClick={this.handleAboutClick}>
                                About
                            </Button>
                        </Grid>
                        <Grid item>
                            <Button
                                variant="contained"
                                size="large"
                                color="primary"
                                onClick={() => {
                                    window.location = "/blog";
                                }}
                            >
                                Blog
                            </Button>
                        </Grid>
                    </Grid>
                </Grid>
                <Grid item>
                    <Grid container direction="row" justify="center" alignItems="center" spacing={1}>
                        <Grid item>
                            <IconButton
                                onClick={() => {
                                    window.open("https://github.com/robranders");
                                }}
                            >
                                <GitHubIcon fontSize="large" />
                            </IconButton>
                        </Grid>
                        <Grid item>
                            <IconButton
                                onClick={() => {
                                    window.open("https://www.linkedin.com/in/robin-branders-b1838a188");
                                }}
                            >
                                <LinkedInIcon fontSize="large" />
                            </IconButton>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
        );
    }
}

export default MainPage;
