import React from "react";
import "../MainPage.css";

import Typography from "@material-ui/core/Typography";
import Container from "@material-ui/core/Container";
import Grid from "@material-ui/core/Grid";
import IconButton from "@material-ui/core/IconButton";
import GitHubIcon from "@material-ui/icons/GitHub";
import LinkedInIcon from "@material-ui/icons/LinkedIn";

import ToolbarComponent from "../../../components/ToolbarComponent";

const profilePic = require("./profilepic.png");

const textStyle = {};

class AboutComponent extends React.Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        document.getElementsByClassName("appBody")[0].classList.add("aboutPage");
    }

    componentWillUnmount() {
        document.getElementsByClassName("aboutPage")[0].classList.remove("aboutPage");
    }

    render() {
        return (
            <>
                <ToolbarComponent />
                <Container maxWidth="lg">
                    <Grid
                        container
                        direction="column"
                        justify="flex-start"
                        alignItems="flex-start"
                        style={{marginTop: "10px"}}
                        spacing={3}
                    >
                        <Grid item>
                            <Typography variant="h2" style={textStyle}>
                                about me
                            </Typography>
                        </Grid>
                        <Grid item>
                            <Grid container direction="row" justify="flex-start" alignItems="flex-start" spacing={4}>
                                <Grid item xs={12} sm={12} md={2}>
                                    <img
                                        src={profilePic}
                                        style={{
                                            width: "100%",
                                            maxWidth: "250px",
                                            height: "auto",
                                            borderRadius: "5px",
                                            border: "1px solid #e1e4e8"
                                        }}
                                    />
                                </Grid>
                                <Grid item xs={12} sm={12} md={10}>
                                    <Grid
                                        container
                                        direction="column"
                                        justify="flex-start"
                                        alignItems="flex-start"
                                        spacing={2}
                                    >
                                        <Grid item>
                                            <Typography variant="body1" style={textStyle}>
                                                Hi, I'm Robin Branders and I am an industrial engineer who is passionate
                                                about coding.
                                            </Typography>
                                            <Typography variant="body1" style={textStyle}>
                                                I'm currently working as a junior full stack MES developer. This
                                                includes designing and building custom React web interfaces as well as
                                                using Thingworx and Operations Hub. Back end is mostly SQL Server, Plant
                                                applications and API development. These soultions monstly need to be
                                                integrated in existing environments.
                                            </Typography>
                                        </Grid>

                                        <Grid
                                            container
                                            direction="row"
                                            justify="center"
                                            alignItems="center"
                                            spacing={1}
                                        >
                                            <Grid item>
                                                <IconButton
                                                    onClick={() => {
                                                        window.open("https://github.com/robranders");
                                                    }}
                                                >
                                                    <GitHubIcon fontSize="large" />
                                                </IconButton>
                                            </Grid>
                                            <Grid item>
                                                <IconButton
                                                    onClick={() => {
                                                        window.open(
                                                            "https://www.linkedin.com/in/robin-branders-b1838a188"
                                                        );
                                                    }}
                                                >
                                                    <LinkedInIcon fontSize="large" />
                                                </IconButton>
                                            </Grid>
                                        </Grid>

                                        {/* <Grid item>
                                            <Typography variant="h5" style={textStyle}>
                                                free time !?
                                            </Typography>
                                        </Grid>
                                        <Grid item>
                                            <Typography variant="body1" style={textStyle}>
                                                Yes I do enjoy non-coding activities mainly riding motorcycles, playing
                                                the guitar and drinking whisk(e)y ! (Albeit not at the same time.
                                                Driving the motorcycle gets in the way of playing the guitar)
                                            </Typography>
                                        </Grid> */}
                                        {/* <Grid item>
                                            <Typography variant="h5" style={textStyle}>
                                                robinbranders.be
                                            </Typography>
                                        </Grid>
                                        <Grid item>
                                            <Typography variant="body1" style={textStyle}>
                                                I like to thinker around with electornics as well and thats what I try
                                                to document on de projects section of my website.
                                            </Typography>
                                        </Grid> */}
                                    </Grid>
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                </Container>
            </>
        );
    }
}

export default AboutComponent;
